*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
}

input[type="search"]::-ms-clear {
  display: none;
}

input[type="search"]::-ms-reveal {
  display: none;
}

input[type="search"]::-webkit-search-decoration {
  display: none;
}

input[type="search"]::-webkit-search-cancel-button {
  display: none;
}

input[type="search"]::-webkit-search-results-button {
  display: none;
}

input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

@media (width <= 900px) {
  html {
    font-size: 50%;
  }

  .navbar__main-links {
    display: none !important;
  }

  .navbar__toggle-btn {
    display: block !important;
  }
}

@media (width <= 720px) {
  html {
    font-size: 40%;
  }
}

@media (width <= 420px) {
  html {
    font-size: 30%;
  }
}

body {
  -webkit-font-smoothing: antialiased;
  font-family: Montserrat, sans-serif;
  line-height: 1.7;
}

a {
  color: inherit;
  text-decoration: none;
}

h2 {
  text-transform: uppercase;
  font-size: 4rem;
  font-weight: 700;
}

h3 {
  font-size: 1.8rem;
  font-weight: 400;
}

p {
  font-size: 1.8rem;
}

.highlight {
  color: #53ae4d;
  font-weight: 700;
}

.lazy_img {
  filter: blur(.6rem);
  transition: all .5s ease-in-out;
}

.lazy_img--loaded {
  filter: blur();
}

.hidden {
  display: none;
}

.no-scroll {
  overflow: hidden;
}

.btn {
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  border-radius: 1.5rem;
  outline: none;
  justify-content: center;
  align-items: center;
  width: max-content;
  padding: 1rem 2rem;
  font-size: 2rem;
  font-weight: 600;
  transition: transform .5s, scale .3s;
  display: flex;
}

.btn--green {
  color: #fff;
  background-color: #53ae4d;
}

.btn--white {
  color: #53ae4d;
  background-color: #fff;
}

.btn--winter {
  color: #fff;
  background-color: #71b5e4;
}

.btn--hero {
  margin: .5rem;
  font-size: 2.5vw;
}

.btn:hover {
  scale: 1.05;
}

.btn:active {
  scale: .95;
}

.contact {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 7rem 2rem;
  display: flex;
}

.contact__title {
  color: #333;
  text-transform: uppercase;
  text-align: center;
}

.contact__form {
  display: block;
}

.contact__form-container {
  text-align: left;
  background-color: #53ae4d;
  border-radius: 1.5rem;
  max-width: 50rem;
  padding: 4rem;
  transition: all .3s ease-in-out;
  box-shadow: .8rem 1.6rem 1.6rem #00000014;
}

.contact__form-container:hover {
  box-shadow: .8rem 1.6rem 1.6rem #00000026;
}

.contact__form-field {
  margin-bottom: 2rem;
}

.contact__form-label {
  color: #fff;
  letter-spacing: .1rem;
  text-align: left;
  margin-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
  display: block;
}

.contact__form-input {
  color: #53ae4d;
  background: #f0f0f0;
  border: .1rem solid #ebebeb;
  border-radius: 1.5rem;
  width: 100%;
  padding: 1.5rem;
  font-size: 1.5rem;
  font-weight: 600;
}

@media (width <= 1133px) {
  .contact .container {
    flex-direction: column;
  }

  .contact__form, .contact__info {
    justify-content: center;
    min-width: 30rem;
  }
}

.header__announcement-bar {
  color: #fff;
  background: linear-gradient(to bottom right, #53ae4d, #73d56c);
  justify-content: space-around;
  padding: .5rem 2rem;
  display: flex;
}

.header__announcement-bar-link {
  color: #fff;
  font-size: 1.5rem;
}

.header__announcement-bar-link-icon {
  margin-right: 1rem;
}

@media (width <= 450px) {
  .header__announcement-bar-text {
    display: none;
  }
}

.navbar__main {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 120rem;
  height: 7rem;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
}

.navbar__main-logo {
  align-items: center;
  display: flex;
}

.navbar__main-logo-title {
  text-transform: capitalize;
  color: #53ae4d;
  font-family: mrs-eaves, serif;
  font-size: 2.5rem;
  font-weight: 400;
}

.navbar__main-logo-img {
  width: 4rem;
}

.navbar__main-links {
  gap: 3rem;
  display: flex;
}

.navbar__main-link {
  color: #53ae4d;
  text-transform: uppercase;
  font-size: 1.8rem;
  font-weight: 700;
  position: relative;
}

.navbar__mobile {
  backdrop-filter: blur(1.5rem);
  background: #53ae4d;
  border-radius: 1.5rem;
  width: 30rem;
  height: 0;
  transition: height .2s cubic-bezier(.175, .885, .32, 1.275);
  position: absolute;
  top: 9rem;
  right: 2rem;
  overflow: hidden;
}

.navbar__mobile--open {
  z-index: 10;
  height: auto;
}

.navbar__mobile-link {
  color: #fff;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  padding: .7rem;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
}

.navbar__mobile-link:hover {
  scale: 1.05;
}

.navbar__toggle-btn {
  color: #53ae4d;
  cursor: pointer;
  z-index: 5;
  font-size: 1.5rem;
  display: none;
}

.modal {
  z-index: 10;
  background: linear-gradient(to bottom right, #4da147, #69e860);
  border-radius: 1.5rem;
  flex-direction: column;
  justify-content: center;
  width: 70%;
  padding: 4rem;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3rem 5rem #0000004d;
}

.modal__description, .modal__text, .btn--modal {
  color: #fff;
}

.modal__overlay, .modal__close {
  position: absolute;
}

.modal__overlay {
  backdrop-filter: blur(.3rem);
  z-index: 5;
  background-color: #0009;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.modal__close {
  cursor: pointer;
  z-index: 11;
  font-size: 2rem;
  top: 1rem;
  right: 1rem;
}

.footer {
  background-color: #53ae4d;
  padding: 2rem;
}

.footer__container {
  max-width: 100rem;
  margin: auto;
}

.footer__row {
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.footer__col {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.footer__separator {
  text-align: center;
  color: #fff;
  width: 2rem;
  margin: 1rem 0;
}

.footer__list {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.footer__item {
  color: #fff;
  font-size: 1.8rem;
}

.footer__title {
  color: #fff;
  font-size: 2.2rem;
  font-weight: 700;
}

.footer__link {
  text-transform: capitalize;
  color: #fff;
  font-weight: 500;
  transition: all .3s;
  display: block;
}

.footer__link:hover {
  color: #3a7936;
}

.footer__social {
  cursor: pointer;
  display: inline-block;
}

.footer__social-link {
  color: #fff;
  margin: 0 .5rem .5rem;
  font-size: 4rem;
  transition: all .3s;
}

.footer__social-link:first-child {
  margin-top: .3rem;
  margin-left: 0;
}

.footer__social-link:hover {
  color: #3a7936;
}

.footer__copyright {
  text-align: center;
  color: #fff;
  margin-top: 2rem;
  font-size: 1.6rem;
}

.footer__copyright-link {
  color: #fff;
}

.footer__copyright-link:hover {
  color: #3a7936;
}

@media (width >= 600px) {
  .footer__row {
    flex-direction: row;
  }

  .footer__col {
    text-align: left;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.header {
  z-index: 5;
  background-color: #fff;
  width: 100%;
  position: absolute;
  box-shadow: 0 -.6rem 1rem .5rem #0003;
}

.header--sticky {
  z-index: 10;
  background-color: #fff;
  width: 100%;
  transition: transform .3s ease-in-out;
  position: fixed;
  box-shadow: 0 -.6rem 1rem .5rem #0003;
}

.hero {
  text-align: center;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.hero__video {
  object-fit: cover;
  z-index: -1;
  width: 100%;
  position: absolute;
}

.hero__title {
  text-shadow: .2rem .2rem .5rem #0000004d;
  z-index: 1;
  padding: 0 3rem;
  font-size: 3vw;
  font-weight: normal;
}

.hero__text {
  z-index: 1;
  margin: 1rem 1rem 4rem;
}

.hero__tagline-1, .hero__tagline-2 {
  text-shadow: .2rem .2rem .5rem #0000004d;
  text-transform: uppercase;
  line-height: 90%;
}

.hero__tagline-1 {
  margin-bottom: 1rem;
  font-size: 5.5vw;
  font-weight: 800;
}

.hero__tagline-2 {
  font-size: 5vw;
}

.hero__underline-1, .hero__underline-2 {
  position: relative;
}

.hero__underline-1:after {
  content: "";
  z-index: -1;
  background-image: url("underline.a1a9c92c.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(35%);
}

.hero__underline-2:after {
  content: "";
  z-index: -1;
  background-image: url("underline-2.5b9db4e8.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(35%);
}

.hero__btns {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero--about {
  text-align: center;
  background-image: url("stripes1.a5cee5a4.jpeg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.hero--about__title {
  color: #fff;
  text-shadow: .2rem .2rem .5rem #0000004d;
  padding: 1rem 3rem;
  font-size: 8rem;
  font-weight: 600;
}

.hero--about__subtitle {
  color: #fff;
  text-shadow: .2rem .2rem .5rem #0000004d;
  padding: 0 3rem;
  font-size: 4rem;
  font-weight: 600;
}

.about__main {
  flex-direction: column;
  display: flex;
}

.about__small-town-service, .about__professional-work {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 7rem 2rem 0;
  display: flex;
}

.small-town-service__header, .professional-work__header {
  text-align: center;
  margin-bottom: 2rem;
}

.small-town-service__container, .professional-work__container {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  display: flex;
}

.professional-work__container {
  flex-direction: row-reverse;
  margin-bottom: 2rem;
}

.small-town-service__description, .professional-work__description {
  background-color: #f0f0f0;
  border-radius: 1.5rem;
  max-width: 300px;
  padding: 1rem;
}

.small-town-service__img-container, .professional-work__img-container {
  flex-basis: 50%;
  width: 100%;
}

.small-town-service__img, .professional-work__img {
  border-radius: 1.5rem;
  width: 100%;
}

@media (width <= 520px) {
  .small-town-service__container, .professional-work__container {
    flex-direction: column;
  }
}

.book {
  text-align: center;
  gap: 2rem;
  padding: 7rem 2rem;
}

.hero--book {
  margin-top: 8rem;
  margin-bottom: 2rem;
}

.services {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 7rem 2rem;
  display: flex;
}

.services__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

.service--active {
  background-color: #3a7936;
  transition: all .5s;
}

.service--raised {
  transition: all .5s;
  transform: translateY(-25%);
}

.services__description {
  background-color: #f0f0f0;
  border-radius: 1.5rem;
  max-width: 70rem;
  padding: 1.5rem;
}

.service {
  background-color: #f0f0f0;
  border-radius: 1.5rem;
  width: 187px;
  padding: 1.5rem;
}

.service__icon {
  color: #fff;
  margin-right: 1rem;
  font-size: 2rem;
}

.transition {
  background-image: url("stripes3.7f6c1366.webp");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  width: 100%;
  min-height: 75vh;
  padding: 7rem 2rem;
  display: flex;
}

.transition__title {
  text-shadow: .2rem .2rem .5rem #0000004d;
  color: #fff;
  text-align: center;
  font-size: 7vw;
}

@media (width <= 800px) {
  .transition {
    background-attachment: scroll;
  }
}

.getting-started {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 7rem 2rem;
  display: flex;
}

.getting-started__steps {
  grid-template-columns: repeat(3, 1fr);
  gap: 6rem;
  display: grid;
}

.step {
  background-color: #f0f0f0;
  border-radius: 1.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 1rem 1.5rem;
  padding: 2rem;
  display: flex;
}

.step__icon {
  color: #53ae4d;
  font-size: 7rem;
}

.step__number {
  text-transform: uppercase;
  color: #333;
  font-size: 2rem;
  font-weight: 700;
}

.step__description {
  text-transform: uppercase;
  color: #000;
  font-size: 1.6rem;
  font-weight: 700;
}

@media (width <= 650px) {
  .getting-started__steps {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}

.reviews {
  background-color: #f0f0f0;
  gap: 2rem;
  padding: 7rem 2rem;
}

.reviews__header {
  text-align: center;
  margin-bottom: 1rem;
}

.mowtrimcleanup {
  background-image: url("stripes-bg-green.3875b84a.webp");
  width: 100%;
}

.mowtrimcleanup .container {
  grid-template-columns: 1fr 1fr;
  gap: 5rem;
  width: 100%;
  max-width: 110rem;
  margin: auto;
  padding: 3rem;
  display: grid;
}

.mowtrimcleanup .container .text {
  color: #fff;
  flex-direction: column;
  place-self: center;
  max-width: 100%;
  display: flex;
}

.mowtrimcleanup .container .text h2 {
  text-transform: uppercase;
  margin-bottom: 2rem;
  font-size: 3rem;
  font-weight: 800;
}

.mowtrimcleanup .container .text p {
  flex-basis: 50%;
  max-width: 40rem;
  font-size: 1rem;
  line-height: 1.75rem;
}

.mowtrimcleanup .swiper {
  max-width: 50rem;
}

@media (width <= 965px) {
  .mowtrimcleanup .container {
    text-align: center;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: none;
    place-content: center;
    gap: 1rem;
  }
}

@media (width <= 685px) {
  .mowtrimcleanup .swiper {
    max-width: 30rem;
  }

  .mowtrimcleanup .container {
    gap: 2rem;
  }

  .mowtrimcleanup .container .text h2 {
    margin-bottom: 1rem;
    font-size: 2.5rem;
  }
}

@media (width <= 405px) {
  .mowtrimcleanup .swiper {
    max-width: 22.5rem;
  }

  .mowtrimcleanup .container .text h2 {
    font-size: 2rem;
  }
}

.key-services {
  flex-direction: column;
  align-items: center;
  margin: 4rem 2rem;
  display: flex;
}

.key-services header {
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 2rem;
  display: flex;
}

.key-services header h2 {
  text-transform: uppercase;
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
}

.cards-container {
  place-items: center;
  margin-bottom: 4rem;
  display: grid;
}

.cards {
  grid-template-columns: repeat(4, minmax(250px, 1fr));
  gap: 1rem;
  display: grid;
}

.cards .card {
  color: #fff;
  text-align: center;
  background-color: #53ae4d;
  border-radius: 1.5rem;
  padding: 2rem;
  overflow: hidden;
}

.cards .card h3 {
  text-transform: uppercase;
  margin-bottom: 1rem;
  transition: all .2s ease-in-out;
  translate: 0 3rem;
}

.cards .card p {
  transition: all .2s ease-in-out;
  translate: 0 30rem;
}

.cards .card:hover > p, .cards .card:hover > h3 {
  translate: 0;
}

@media (width <= 1115px) {
  .cards {
    grid-template-columns: repeat(2, minmax(25rem, 30rem));
    gap: 2rem;
  }
}

@media (width <= 650px) {
  .cards {
    grid-template-columns: repeat(1, minmax(25rem, 30rem));
    gap: 2rem;
  }

  .key-services header h2 {
    font-size: 2rem;
  }
}

@media (hover: none) {
  .cards .card p, .cards .card h3 {
    translate: 0;
  }
}

